import React from "react"
import PropTypes from "prop-types";
import ParagraphType from "../../../core/enum/ParagraphType"
import Color from "../../../core/enum/Color"

const CMSParagraphConsoleComponent = (props) => {

    const toHTML = (content) => {
        return {__html: content};
    };

    const innerPadding = (!!props.innerPadding) ? 'u-padding-' + props.innerPadding + ' u-padding-top-' + props.innerPadding : '';

    const title = !!props.title ? props.title : (props.type === ParagraphType.WARN || props.type === ParagraphType.ERROR) ? 'Uwaga' : '';

    const color = ParagraphType.getColor(props.type);

    return (
        <div className={`l-page-row u-padding-${props.outerPadding} u-padding-top-${props.outerPadding}`}>
            <div className={`opl-console l-row ${innerPadding}`} style={{borderLeft: `10px solid ${color}`}}>
                {
                    iconSection({...props, color})
                }
                <div className="u-padding-all-m l-col l-col--auto" >
                    {
                        !!title && <div className="u-padding-s u-font-bold">{title}</div>
                    }
                    <div dangerouslySetInnerHTML={toHTML(props.content)}/>
                </div>
                {
                    !!props.reactContent &&
                    <div className="u-padding-all-m l-col l-col-3 u-right" >
                        {props.reactContent}
                    </div>
                }
            </div>
        </div>
    )
};

const iconSection = ({type, color}) => {
   switch(type){
       case ParagraphType.WARN:
       case ParagraphType.ERROR:
       case ParagraphType.INFO:
           return (
               <div className="l-col l-col--auto">
                    <span className={`g-icon g-icon--only g-icon--${type.toLowerCase()} g-icon--m-s u-padding u-padding-right-s u-padding-top-m`}
                          style={{color}}
                          aria-hidden="true"/>
               </div>
           );
       default:
           return null;
   }
}

CMSParagraphConsoleComponent.propTypes = {
    content: PropTypes.string.isRequired,
    type: PropTypes.string,
    title: PropTypes.string,
    outerPadding: PropTypes.string,
    innerPadding: PropTypes.string
};

CMSParagraphConsoleComponent.defaultProps = {
    outerPadding: 'l',
    type: ParagraphType.CONSOLE
};

export default CMSParagraphConsoleComponent;